// Bularga tega ko'rmang 💣

import 'core-js/stable'
import 'regenerator-runtime/runtime'

// Kod yozishni shu yerdan boshlaysiz

// constants
const minute = document.querySelector('#minute')
const second = document.querySelector('#second')
const settings = document.querySelector('#settings__button')
const modal = document.querySelector('#modal')
const close = document.querySelector('#close')
const apply = document.querySelector('#apply')
const wrapperCircle = document.querySelector('#wrapper__circle')
const buttonList = document.querySelector('#button__list')
const buttonFontsList = document.querySelector('#font__circles')
const buttonColorsList = document.querySelector('#color__circles')

const start = document.querySelector('#start')

// buttonList selectors
let pomodoroInput = document.querySelector('#pomodoro__input')
const pomodoroUp = document.querySelector('#pomodoro__up')
const pomodoroDown = document.querySelector('#pomodoro__down')

let shortBreakInput = document.querySelector('#shortBreak__input')
const shortBreakUp = document.querySelector('#shortBreak__up')
const shortBreakDown = document.querySelector('#shortBreak__down')

let longBreakInput = document.querySelector('#longBreak__input')
const longBreakUp = document.querySelector('#longBreak__up')
const longBreakDown = document.querySelector('#longBreak__down')
// buttonList selectors

const fontsName = document.getElementsByName('fonts')

//variables for change buttons
let timer
let buttonID = 0
let buttonItems = buttonList.children
let currentColor = 'red'
// /constants

//functions

const applyModal = () => {
  modal.classList.add('hidden')
}

const changeButton = e => {
  if (e.target.id === 'button__list') return
  const newButtonItems = [...buttonItems]
  buttonID = +e.target.dataset.buttons
  buttonItems = buttonList.children

  // add and remove classes

  newButtonItems.forEach(e => {
    return (
      e.classList.remove(`bg-${currentColor}`, 'text-gray-dark'),
      e.classList.add('text-gray', 'opacity-40')
    )
  })

  buttonItems[buttonID].classList.add(`bg-${currentColor}`, 'text-gray-dark')
  buttonItems[buttonID].classList.remove('opacity-40')

  changeValue(buttonID)
  stopTimer()
  start.children[0].textContent = 'start'
  start.dataset.condition = 'pause'
}

// change values
function changeValue(buttonID) {
  if (buttonID === 0) {
    minute.textContent = '25'
    second.textContent = '00'
  }
  if (buttonID === 1) {
    minute.textContent = '05'
    second.textContent = '00'
  }
  if (buttonID === 2) {
    minute.textContent = '10'
    second.textContent = '00'
  }
}

function startTimer(e) {
  if (minute.textContent === 0 && second.textContent === 0) {
    second.textContent = '00'
    stopTimer()
  } else if (second.textContent === '00' || second.textContent === 0) {
    minute.textContent--
    second.textContent = 59

    if (minute.textContent < 10) {
      minute.textContent = '0' + minute.textContent
    }
  } else {
    second.textContent--
    if (second.textContent < 10) {
      second.textContent = '0' + second.textContent
    }
  }

  if (minute.textContent === '00' && second.textContent === '00') {
    stopTimer()
    e.target.dataset.condition = 'restart'
    e.target.textContent = 'restart'
  }
}

function stopTimer() {
  clearInterval(timer)
}

// 2.3 part --------------------------------------------

function changeInputValue(e, action) {
  if (!buttonID && e === 'pomodoro') {
    action === 'inc' && (pomodoroInput.value = +pomodoroInput.value + 1)
    action === 'dec' && (pomodoroInput.value = +pomodoroInput.value - 1)
    minute.textContent = pomodoroInput.value
    second.textContent = '00'
  }
  if (buttonID === 1 && e === 'shortBreak') {
    action === 'inc' && (shortBreakInput.value = +shortBreakInput.value + 1)
    action === 'dec' && (shortBreakInput.value = +shortBreakInput.value - 1)
    minute.textContent = shortBreakInput.value
    second.textContent = '00'
  }
  if (buttonID === 2 && e === 'longBreak') {
    action === 'inc' && (longBreakInput.value = +longBreakInput.value + 1)
    action === 'dec' && (longBreakInput.value = +longBreakInput.value - 1)
    minute.textContent = longBreakInput.value
    second.textContent = '00'
  }
}

const changeFont = e => {
  if (e.target.name === 'font__circles') return

  if (e.target.checked) {
    fontsName.forEach(element => {
      element.parentNode.classList.add(
        'bg-gray-light',
        'text-black',
        'opacity-70',
      )
      element.parentNode.classList.remove('bg-black', 'text-white')
    })

    e.target.parentNode.classList.remove(
      'bg-gray-light',
      'text-black',
      'opacity-70',
    )
    e.target.parentNode.classList.add('bg-black', 'text-white')

    document.body.classList.remove(
      'font-kumbhSans',
      'font-spaceMono',
      'font-robotoSlab',
    )

    document.body.classList.add(`font-${e.target.id}`)
    if (e.target.id === 'spaceMono') {
      buttonList.classList.remove('w-[440px]')
      buttonList.classList.add('w-[470px]')
    } else {
      buttonList.classList.add('w-[440px]')
      buttonList.classList.remove('w-[470px]')
    }
  }
}

const changeColor = e => {
  const newButtonItems = [...buttonItems]
  if (e.target.id === 'color__circles') return

  if (e.target.checked) {
    const colorsName = document.getElementsByName('colors')
    if (e.target.id === 'red') {
      currentColor = 'red'
    } else if (e.target.id === 'teal') {
      currentColor = 'teal'
    } else if (e.target.id === 'purple') {
      currentColor = 'purple'
    }
    colorsName.forEach(element => {
      element.nextElementSibling.classList.add('hidden')
    })

    e.target.nextElementSibling.classList.remove('hidden')

    newButtonItems.forEach(e => {
      e.classList.remove('bg-red', 'bg-teal', 'bg-purple', 'text-gray-dark'),
        e.classList.add('text-gray', 'opacity-40')
    })

    buttonItems[buttonID].classList.add(`bg-${currentColor}`, 'text-gray-dark')
    buttonItems[buttonID].classList.remove('opacity-40')
    wrapperCircle.classList.remove('border-red', 'border-teal', 'border-purple')
    wrapperCircle.classList.add(`border-${currentColor}`)
  }
}

// 2.3 part finish --------------------------------------------

//  /functions

// listeners
settings.addEventListener('click', () => {
  modal.classList.remove('hidden')
})
close.addEventListener('click', () => {
  modal.classList.add('hidden')
})
apply.addEventListener('click', applyModal)
buttonList.addEventListener('click', changeButton)
buttonFontsList.addEventListener('click', changeFont)
buttonColorsList.addEventListener('click', changeColor)
start.addEventListener('click', e => {
  if (e.target.dataset.condition === 'pause') {
    timer = setInterval(() => startTimer(e), 1000)
    e.target.textContent = 'pause'
    e.target.dataset.condition = 'start'
    return
  }
  if (e.target.dataset.condition === 'start') {
    stopTimer()
    e.target.textContent = 'start'
    e.target.dataset.condition = 'pause'
    return
  }
  if (e.target.dataset.condition === 'restart') {
    e.target.textContent = 'start'
    e.target.dataset.condition = 'pause'
    changeValue(buttonID)
    return
  }
})

// sum and substr input values
pomodoroUp.addEventListener('click', () => changeInputValue('pomodoro', 'inc'))
shortBreakUp.addEventListener('click', () =>
  changeInputValue('shortBreak', 'inc'),
)
longBreakUp.addEventListener('click', () =>
  changeInputValue('longBreak', 'inc'),
)

pomodoroDown.addEventListener('click', () =>
  changeInputValue('pomodoro', 'dec'),
)
shortBreakDown.addEventListener('click', () =>
  changeInputValue('shortBreak', 'dec'),
)
longBreakDown.addEventListener('click', () =>
  changeInputValue('longBreak', 'dec'),
)
pomodoroInput.addEventListener('input', () => {
  if (!buttonID) minute.textContent = pomodoroInput.value
})
shortBreakInput.addEventListener('input', () => {
  if (buttonID === 1) minute.textContent = shortBreakInput.value
})
longBreakInput.addEventListener('input', () => {
  if (buttonID === 2) minute.textContent = longBreakInput.value
})
//  /sum and substr input values

//  //listeners
